// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Table Columns
import * as FileSaver from "file-saver"
import * as XLSX from "xlsx"
import { columns } from './columns'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Download, MoreVertical, RefreshCcw } from 'react-feather'

// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Input,
    Label,
    CardBody,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Button
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import moment from 'moment'


// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, exportList, setDateVal, startDate }) => {
    // ** Converts table to CSV
    return (
        <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
            <Row>
                <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
                    <div className='d-flex align-items-center w-100'>
                        <label htmlFor='rows-per-page'>Show</label>
                        <Input
                            className='mx-50'
                            type='select'
                            id='rows-per-page'
                            value={rowsPerPage}
                            onChange={handlePerPage}
                            style={{ width: '5rem' }}
                        >
                            <option value='10'>10</option>
                            <option value='25'>25</option>
                            <option value='50'>50</option>
                        </Input>
                        <label htmlFor='rows-per-page'>Entries</label>
                    </div>
                </Col>
                <Col
                    xl='6'
                    className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
                >
                    <div className='pe-1'>
                        <Flatpickr
                            onChange={(date) => {
                                setDateVal(date)
                            }}
                            value={startDate}
                            options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                            className={'due-date-picker me-1'} />
                    </div>
                    <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
                        <label className='mb-0' htmlFor='search-invoice'>
                            Search:
                        </label>
                        <Input
                            id='search-invoice'
                            className='ms-50 w-100'
                            type='text'
                            value={searchTerm}
                            onChange={e => handleFilter(e.target.value)}
                        />
                    </div>

                    <div className='d-flex align-items-center table-header-actions'>

                        <UncontrolledDropdown className='ms-1'>
                            <DropdownToggle color='secondary' caret outline>
                                <MoreVertical className='font-small-4 me-50' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem onClick={() => { exportList() }} className='w-100'>
                                    <Download className='font-small-4 me-50' />
                                    <span className='align-middle'>Export</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>

                    </div>
                </Col>
            </Row>
        </div>
    )
}

const TaskList = () => {

    // ** States
    const [sort, setSort] = useState('desc')
    const [searchTerm, setSearchTerm] = useState('')
    const [data, setData] = useState([])
    const [startDate, setStartDate] = useState(moment().startOf('day').valueOf())
    const [sortColumn, setSortColumn] = useState('createdon')
    const [rowsPerPage, setRowsPerPage] = useState(10)


    const getData = (params) => {
        axios.post('/employeeattendances/dailylist', params)
            .then((res) => {
                setData(res.data.employeeattendances)
            }).catch((err) => {
                console.log(err)
            })
    }

    const msToTime = (ms) => {

        let seconds = ms / 1000
        const hours = parseInt(seconds / 3600)
        seconds = seconds % 3600
        const minutes = parseInt(seconds / 60)
        seconds = seconds % 60
        return `${hours} Hrs : ${minutes} Mins`
    }

    const timeFormat = (value) => {
        if (!value) {
            return '-'
        }
        if (value.length === 13) {
            return moment(value, 'x').format("hh:mm A")
        } else {
            return moment.unix(value).format("hh:mm A")
        }
    }

    const exportTask = (params) => {
        axios.post('/employeeattendances/dailylist', params)
            .then(async (res) => {

                const result = await res.data.employeeattendances.map((obj) => {

                    const data = {}
                    data['name'] = obj.name
                    data['attendance'] = obj.punchin ? 'Present' : 'Absent'
                    data['punchin'] = timeFormat(obj.punchin)
                    data['punchout'] = timeFormat(obj.punchout)
                    data['totalhours'] = msToTime(obj.totalhours)

                    return data
                })

                const ws = await XLSX.utils.json_to_sheet(result, { origin: 'A2', skipHeader: true })
                const Heading = [['Name', 'Attendance', 'Clock In', 'Clock Out', 'Total Hours']]
                XLSX.utils.sheet_add_aoa(ws, Heading)
                const wb = { Sheets: { data: ws }, SheetNames: ["data"] }
                const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" })
                const data = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8" })
                FileSaver.saveAs(data, 'Daily Attendance_Report.xlsx')
            }).catch((err) => {
                console.log(err)
            })
    }

    useEffect(async () => {
        await
            getData({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: rowsPerPage,
                date: startDate
            })

    }, [data.length, sort, sortColumn])

    const handlePerPage = async e => {
        const value = parseInt(e.currentTarget.value)
        await
            getData({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: value,
                date: startDate
            })

        setRowsPerPage(value)
    }

    const exportList = async () => {

        await exportTask({
            sort,
            sortColumn,
            q: searchTerm,
            date: startDate,
            export: true
        })

    }

    // ** Function in get data on search query change
    const handleFilter = async val => {
        setSearchTerm(val)
        await getData({
            sort,
            q: val,
            sortColumn,
            perPage: rowsPerPage,
            date: startDate
        })

    }


    // ** Table data to render
    const dataToRender = () => {
        const filters = {
            q: searchTerm
        }

        const isFiltered = Object.keys(filters).some(function (k) {
            return filters[k].length > 0
        })

        if (data.length > 0) {
            return data
        } else if (data.length === 0 && isFiltered) {
            return []
        }
    }

    const handleSort = async (column, sortDirection) => {
        setSort(sortDirection)
        setSortColumn(column.sortField)

        await getData({
            sort,
            sortColumn,
            q: searchTerm,
            perPage: rowsPerPage,
            date: startDate
        })
    }

    const setDateVal = (val) => {

        const stdate = moment(new Date(val[0])).startOf('d').valueOf()
        setStartDate(stdate)
        if (stdate !== '') {
            getData({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: rowsPerPage,
                date: stdate
            })
        }
    }

    return (
        <Fragment>
            <Card className='overflow-hidden'>
                <div className='react-dataTable'>
                    <DataTable
                        noHeader
                        subHeader
                        sortServer
                        responsive
                        columns={columns}
                        onSort={handleSort}
                        sortIcon={<ChevronDown />}
                        className='react-dataTable'
                        data={dataToRender()}
                        subHeaderComponent={
                            <CustomHeader
                                searchTerm={searchTerm}
                                rowsPerPage={rowsPerPage}
                                handleFilter={handleFilter}
                                handlePerPage={handlePerPage}
                                exportList={exportList}
                                setDateVal={setDateVal}
                                startDate={startDate}
                                setStartDate={setStartDate}
                            />
                        }
                    />
                </div>
            </Card>


        </Fragment>
    )
}

export default TaskList
