// ** Custom Components
import Avatar from '@components/avatar'
import moment from 'moment'
import { Link } from 'react-router-dom'
import { Badge, Col } from 'reactstrap'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name ? row.name?.charAt(0) : 'T'}
    />
  )
}

const msToTime = (ms) => {

  let seconds = ms / 1000
  const hours = parseInt(seconds / 3600)
  seconds = seconds % 3600
  const minutes = parseInt(seconds / 60)
  seconds = seconds % 60
  return `${hours} Hrs : ${minutes} Mins`
}

const timeFormat = (value) => {
  if (!value) {
    return '-'
  }
  if (value.length === 13) {
    return moment(value, 'x').format("hh:mm A")
  } else {
    return moment.unix(value).format("hh:mm A")
  }
}


export const columns = [
  {
    name: 'Name',
    sortable: false,
    maxWidth: '250px',
    cell: row => (
      <Col tag={Link} to={`/reports/timesheetlogs/${row.id}`} className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='user-info text-truncate'>
          <span className='d-block fw-bold text-truncate'>{row.name}</span>
        </div>
      </Col>
    )
  },
  {
    name: 'Attendance',
    sortable: false,
    minWidth: '250px',
    selector: row => (
      row.punchin ? <Badge color='light-success'>Present</Badge> : <Badge color='light-danger'>Absent</Badge>
    )
  },
  {
    name: 'Clock In',
    minWidth: '132px',
    sortable: false,
    cell: row => <span className='text-capitalize'>{timeFormat(row.punchin)}</span>
  },
  {
    name: 'Clock Out',
    minWidth: '132x',
    sortable: false,
    selector: row => row.priority,
    cell: row => <span className='text-capitalize'>{timeFormat(row.punchout)}</span>
  },
  {
    name: 'Total Hours',
    minWidth: '138px',
    sortable: false,
    cell: row => (
      <span>{msToTime(row.totalhours)}</span>
    )
  }
]
